/* --------------------------------------------------------------------------
	#. Button General
  -------------------------------------------------------------------------- */
.adfy-wl-btn,
.adfy-wl-btn {
  fill: var(--adfy_wishlist_wishlist_btn_text_color);
  color: var(--adfy_wishlist_wishlist_btn_text_color);
  background: transparent;
  background-color: var(--adfy_wishlist_wishlist_btn_bg_color);
  transition: all 0.5s ease;
}
.adfy-wl-btn:hover,
.adfy-wl-btn:hover {
  outline: none;
  fill: var(--adfy_wishlist_wishlist_btn_text_color_hover);
  color: var(--adfy_wishlist_wishlist_btn_text_color_hover);
  background: transparent;
  background-color: var(--adfy_wishlist_wishlist_btn_bg_color_hover);
}
.adfy-wl-btn:focus,
.adfy-wl-btn:focus {
  outline: none;
}

p.adfy-wl-btn .button,
.adfy-wl-btn:not(p.adfy-wl-btn) {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  font-weight: normal;
  cursor: pointer;
  box-shadow: none;
  border-style: none;
  border-width: 0;
  border-color: transparent;
  transition: all 0.5s ease;
}
p.adfy-wl-btn .button:focus, p.adfy-wl-btn .button:hover,
.adfy-wl-btn:not(p.adfy-wl-btn):focus,
.adfy-wl-btn:not(p.adfy-wl-btn):hover {
  box-shadow: none;
  border-style: none;
  border-width: 0;
  border-color: transparent;
}
p.adfy-wl-btn .button.adfy-wl-clear-btn-style,
.adfy-wl-btn:not(p.adfy-wl-btn).adfy-wl-clear-btn-style {
  padding: 0;
  margin: 0;
  color: #444444;
  border: none;
  cursor: pointer;
  box-shadow: none;
  border-color: none;
  text-decoration: none;
  background-color: transparent;
}
p.adfy-wl-btn .button.adfy-wl-clear-btn-style:hover,
.adfy-wl-btn:not(p.adfy-wl-btn).adfy-wl-clear-btn-style:hover {
  color: #444444;
  border: none;
  box-shadow: none;
  text-decoration: none;
  background-color: transparent;
}

body.addonify-wishlist-btn-over-image .woocommerce-LoopProduct-link,
body.addonify-wishlist-btn-over-image .wc-block-product {
