/* --------------------------------------------------------------------------
	#. Wishlist modal
  -------------------------------------------------------------------------- */
.adfy-wl-modal-content-is-open {
  overflow: hidden;
}

#adfy-wl-modal-content-wrapper {
  box-sizing: border-box;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

#adfy-wl-modal-content-wrapper {
  display: none;
  place-items: center;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  transition: all 0.5s ease;
  background-color: var(--adfy_wishlist_popup_modal_overlay_bg_color);
  z-index: 1000;
}
#adfy-wl-modal-content-wrapper .adfy-wl-modal-content {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  width: 400px;
  min-height: 350px;
  text-align: center;
  padding: 30px;
  border-radius: 5px;
  box-sizing: border-box;
  box-shadow: 0px 0px 65px rgba(0, 0, 0, 0.2);
  background-color: var(--adfy_wishlist_popup_modal_bg_color);
  /* Wide */
}
