/**
*
* Loading state.
*
*/
#adfy-wl-modal-content-wrapper.loading .adfy-wl-modal-content {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
#adfy-wl-modal-content-wrapper.loading .adfy-wl-modal-content * {
  display: none;
  opacity: 0;
  visibility: hidden;
}
#adfy-wl-modal-content-wrapper.loading .adfy-wl-modal-content::before {
