#adfy-wl-content-wrapper #addonify-wishlist-table td.actions p.adfy-wl-btn a.button {
  display: inline-flex;
  margin: 0;
  padding: 10px 15px;
  font-weight: normal;
  text-decoration: none;
  line-height: 1;
}
#adfy-wl-content-wrapper #addonify-wishlist-table td.actions p.adfy-wl-btn .button:hover,
#adfy-wl-content-wrapper #addonify-wishlist-table td.actions p.adfy-wl-btn a.button:hover {
  outline: none;
  text-decoration: none;
}
#adfy-wl-content-wrapper #addonify-wishlist-table .adfy-wl-table-toolbars {
  display: flex;
  justify-content: flex-end;
}
#adfy-wl-content-wrapper #addonify-wishlist-page-toolbar {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
}
#adfy-wl-content-wrapper #addonify-wishlist_spinner {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 100%;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 100;
  background-color: rgb(255, 255, 255);
  transition: all 0.5s ease;
}
#adfy-wl-content-wrapper #addonify-wishlist_spinner svg {
  display: inline-flex;
  position: relative;
  color: #444444;
  width: 32px;
  height: 32px;
  animation: AddonifyWishlistSpin 0.8s infinite ease-in-out;
}
#adfy-wl-content-wrapper #adfy-wl-notice {
  display: block;
}
#adfy-wl-content-wrapper #adfy-wl-notice > p {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}
#adfy-wl-content-wrapper #addonify-wishlist-empty {
  display: block;
}

/* -------------------------------------------------------------------------- 
	#. Media Query
  -------------------------------------------------------------------------- */
@media (max-width: 768px) {
  #adfy-wl-content-wrapper #addonify-wishlist-table {
    border: none;
  }
  #adfy-wl-content-wrapper #addonify-wishlist-table thead {
    display: none;
  }
  #adfy-wl-content-wrapper #addonify-wishlist-table tbody tr {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    position: relative;
    flex-direction: column;
    margin-bottom: 30px;
    padding: 20px 20px 20px 90px;
    border: 1px solid #EEEEEE;
    border-radius: 10px;
  }
  #adfy-wl-content-wrapper #addonify-wishlist-table tbody tr td {
    padding: 10px 0;
    border: none;
    line-height: 1.4;
    border-bottom: 1px dotted #EEEEEE;
  }
  #adfy-wl-content-wrapper #addonify-wishlist-table tbody tr td:last-child {
    padding-bottom: 0;
    border-bottom: none;
  }
  #adfy-wl-content-wrapper #addonify-wishlist-table tbody tr td.remove {
    position: absolute;
    top: 15px;
    left: auto;
    right: 10px;
    width: 30px;
    padding: 0;
    border: 0;
  }
  #adfy-wl-content-wrapper #addonify-wishlist-table tbody tr td.image {
    position: absolute;
    display: inline-flex;
    left: 20px;
    right: auto;
    top: 30px;
    border: 0;
    padding: 0;
    width: 55px;
    overflow: hidden;
  }
  #adfy-wl-content-wrapper #addonify-wishlist-table tbody tr td.image a {
    display: inline-flex;
  }
  #adfy-wl-content-wrapper #addonify-wishlist-table tbody tr td.image a img {
    display: inline-flex;
    width: 55px;
    object-fit: cover;
    object-position: center;
    border-radius: 2px;
  }
  #adfy-wl-content-wrapper #addonify-wishlist-table tbody tr td.name {
    padding-right: 40px;
  }
  #adfy-wl-content-wrapper #addonify-wishlist-table tbody tr td.actions .adfy-wl-btn a {
    height: auto;
    padding: 13px 15px;
  }
  #adfy-wl-content-wrapper #addonify-wishlist-table tbody tr:last-child {
    margin-bottom: 0;
  }
  #adfy-wl-content-wrapper #addonify-wishlist-page-toolbar {
    justify-content: center;
  }
}
/*
------------------------------------------------------------------
    Wishlist page section
------------------------------------------------------------------
*/
.adfy-wl-page-section {
  text-align: center;
}
.adfy-wl-page-section .adfy-wl-section-icon svg {
  height: 4em;
  width: 4em;
}
.adfy-wl-page-section .adfy-wl-section-content {
  margin-top: 30px;
}
.adfy-wl-page-section .adfy-wl-section-content .adfy-wl-section-header {
  margin-bottom: 20px;
}
.adfy-wl-page-section .adfy-wl-section-content p {
  margin-bottom: 0;
}
.adfy-wl-page-section .adfy-wl-section-content p a.adfy-wl-page-btn {
  display: inline-block;
  padding: 5px 30px;
  text-decoration: none;
}

/* --------------------------------------------------------------------------
	#. Cart table
  -------------------------------------------------------------------------- */
.woocommerce-cart-form .addonify_wishlist-cart-item-add-to-wishlist {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  margin-top: 5px;
  height: auto;
  padding: 10px 20px;
}
.woocommerce-cart-form .addonify_wishlist-cart-item-add-to-wishlist.adfy-wl-hide {
  display: none !important;
}

/* --------------------------------------------------------------------------
	Hide/show
  -------------------------------------------------------------------------- */
.adfy-wl-hide {
  display: none;
}

.adfy-wl-show {
  display: block;
}

/* --------------------------------------------------------------------------
	Visibility
  -------------------------------------------------------------------------- */
.adfy-wishlist-visibility-hidden {
  opacity: 0;
  visibility: hidden;
}

.adfy-wishlist-visibility-visible {
  opacity: 1;
  visibility: visible;
}

/* --------------------------------------------------------------------------
	Spinner
  -------------------------------------------------------------------------- */
.adfy-wishlist-spinner {
  position: relative;
}

/* --------------------------------------------------------------------------
	Notices
  -------------------------------------------------------------------------- */
#adfy-wl-notice {
  position: relative;
}
#adfy-wl-notice > p {
  position: relative;
  display: block;
  color: var(--adfy_wishlist_product_removed_notice_text_color);
  padding: 15px 20px;
  margin-bottom: 30px;
  font-size: var(--adfy_wishlist_product_removed_notice_font_size);
  font-weight: normal;
  overflow: hidden;
  border-radius: 3px;
  background-color: var(--adfy_wishlist_product_removed_notice_bg_color);
}
#adfy-wl-notice > p #addonify-wishlist-undo-deleted-product-link {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 7px 10px;
  margin: 0 0 0 3px;
  font-size: calc(var(--adfy_wishlist_product_removed_notice_font_size) - 1px);
  line-height: 1;
  font-weight: normal;
  text-decoration: none;
  cursor: pointer;
  border-radius: 30px;
  color: var(--adfy_wishlist_product_removed_notice_undo_btn_text_color);
  background-color: var(--adfy_wishlist_product_removed_notice_undo_btn_bg_color);
  transition: all 0.5s ease;
}
#adfy-wl-notice > p #addonify-wishlist-undo-deleted-product-link:hover {
  color: var(--adfy_wishlist_product_removed_notice_undo_btn_hover_text_color);
  background-color: var(--adfy_wishlist_product_removed_notice_undo_btn_hover_bg_color);
  text-decoration: none;
}

#addonify-wishlist-empty #addonify-empty-wishlist-para {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  gap: 5px;
}

.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
}

/* -------------------------------------------------------------------------- 
	#. Pulse
  -------------------------------------------------------------------------- */
@keyframes AddonifyWishlistPulse {
  0% {
    transform: scale(0.9);
  }
  70% {
    transform: scale(1);
    box-shadow: 0 0 0 50px rgba(90, 153, 212, 0);
  }
  100% {
    transform: scale(0.9);
    box-shadow: 0 0 0 0 rgba(90, 153, 212, 0);
  }
}
/* -------------------------------------------------------------------------- 
	#. Spin
  -------------------------------------------------------------------------- */
@keyframes AddonifyWishlistSpin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* -------------------------------------------------------------------------- 
	#. FadeInUp
  -------------------------------------------------------------------------- */
@keyframes AddonifyWishlistFadeInUp {
  0% {
    opacity: 0;
    transform: translateY(40px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}